import React, {useEffect, useState} from "react";
import {
    AppBar,
    Button,
    Container,
    IconButton,
    Menu,
    MenuItem,
    styled,
    Switch,
    Toolbar,
    Typography,
} from "@mui/material";
import Brightness7Icon from "@mui/icons-material/Brightness7";
import Brightness4Icon from "@mui/icons-material/Brightness4";
import MenuIcon from "@mui/icons-material/Menu";
import {useLocation} from "react-router-dom";
import GlassEffect from "../theme/GlassEffect";

const StyledToolbar = styled(Toolbar)(({theme}) => ({
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
    padding: "8px 12px",
    flexShrink: 0,
}));

const HeaderComponent: React.FC<{ mode: string; setMode: (mode: string) => void }> = ({mode, setMode}) => {
    const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
    const [pageName, setPageName] = React.useState<string | undefined>(undefined);
    const [headerHeight, setHeaderHeight] = useState<number>(0);

    const open = Boolean(anchorEl);

    const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => setAnchorEl(event.currentTarget);
    const handleClose = () => setAnchorEl(null);
    const toggleTheme = () => setMode(mode === "light" ? "dark" : "light");

    const location = useLocation();

    useEffect(() => {
        let currentPage: string | undefined = location.pathname;
        if (currentPage === "/") currentPage = "Home";
        else currentPage = currentPage.split("/").pop();

        if (currentPage) currentPage = currentPage.charAt(0).toUpperCase() + currentPage.slice(1);

        setPageName(currentPage?.replaceAll("-", " "));
    }, [location]);

    useEffect(() => {
        const header = document.getElementById("header-component");
        if (header) {
            setHeaderHeight(header.offsetHeight);
        }
    }, []);

    return (
        <>
            <AppBar
                id="header-component"
                position="fixed"
                sx={{
                    boxShadow: "none",
                    bgcolor: "transparent",
                    backgroundImage: 'none',
                    zIndex: 1200,
                }}
                color="default"
            >
                <Container maxWidth="lg">
                    <GlassEffect>
                        <StyledToolbar variant="dense" disableGutters>
                            <IconButton
                                id="hamburger-nav-menu-button"
                                size="large"
                                edge="start"
                                color="primary"
                                aria-label="menu"
                                sx={{mr: 2}}
                                aria-controls={open ? "menu-toggle" : undefined}
                                aria-haspopup="true"
                                aria-expanded={open ? "true" : undefined}
                                onClick={handleClick}
                            >
                                <MenuIcon/>
                            </IconButton>
                            <Menu
                                id="hamburger-nav-menu"
                                anchorEl={anchorEl}
                                open={open}
                                onClose={handleClose}
                                MenuListProps={{
                                    "aria-labelledby": "basic-button",
                                }}
                            >
                                <MenuItem onClick={handleClose}>Profile</MenuItem>
                                <MenuItem onClick={handleClose}>My account</MenuItem>
                                <MenuItem onClick={handleClose}>Logout</MenuItem>
                            </Menu>
                            <Typography variant="h6" component="div" sx={{flexGrow: 1}} color="textSecondary">
                                {pageName}
                            </Typography>
                            <Button color="inherit" variant="text">
                                <Typography variant="inherit" component="div" sx={{flexGrow: 1}} color="textSecondary">
                                    Login
                                </Typography>
                            </Button>
                            <Switch
                                checked={mode === "dark"}
                                onChange={toggleTheme}
                                inputProps={{"aria-label": "controlled"}}
                                icon={<Brightness7Icon sx={{fontSize: 24, position: "relative", bottom: "2px"}}/>}
                                checkedIcon={<Brightness4Icon
                                    sx={{fontSize: 24, position: "relative", bottom: "2px"}}/>}
                            />
                        </StyledToolbar>
                    </GlassEffect>
                </Container>
            </AppBar>
        </>
    );
};

export default HeaderComponent;
